.App-header{
  display:flex; background:#090909;
  color: #fff;
  padding-left:20px;
  font-size:0.6rem;
}
div{ box-sizing: border-box;}
.list{
  padding: 16px 20px;
  border: 1px dashed #ccc;
}
span{
  font-size: 0.6rem;
  font-weight:bold;
}
h3{
  color:#333;  cursor:pointer;
}
span:first-child{
  cursor:pointer; 
  color:rgb(22, 252, 213);
}
.text-box{
  display:flex;
  justify-content:center;
  margin:16px 0;
}
.text-box input{
  padding:4px 8px;
  border:1px solid #ccc;
  height:20px;
}
.text-box button{
  height:30px;
  border:0px;
  background:#f98;
  color:#fff;
  border-radius:4px;
  margin-left:8px;
}

.detail{
  width:500px; height:auto;
  margin:20px auto;
  padding:20px;
  border:2px solid #ccc;
  background:#cdee;
  text-align: center;
}